.survey-form {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.survey-form div {
  margin-bottom: 10px;
}

.survey-form label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.survey-form input[type="text"],
.survey-form input[type="number"],
.survey-form input[type="email"] {
  width: calc(50% - 10px);
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.survey-form input[type="submit"] {
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}

.survey-form input[type="submit"]:hover {
  background-color: #0056b3;
}

